/* src/components/Header.css */
.header-navbar {
    background-color: #2c3e50; /* Updated background color for better contrast */
    border-bottom: 3px solid #18bc9c; /* Accent color for the border */
  }
  
  .header-navbar .navbar-brand {
    color: #ecf0f1; /* Light text color */
    font-size: 1.75em; /* Slightly larger font size */
    font-weight: bold;
    text-transform: uppercase;
  }
  
  .header-navbar .nav-link {
    color: #ecf0f1;
    margin-right: 20px; /* Increased margin for better spacing */
    transition: color 0.3s ease-in-out;
  }
  
  .header-navbar .nav-link:hover {
    color: #18bc9c; /* Matching accent color on hover */
  }
  
  .header-navbar .navbar-toggler {
    border: none;
  }
  
  .header-navbar .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba%28%255%2C%255%2C%255%2C0.7%29' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
  }
  